html,
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
  /* font-family: "Monoton", cursive; */
}

.sign-up-wrapper {
  background: linear-gradient(
    90.51deg,
    #fbfbfb -2.93%,
    #2457ba -2.92%,
    #c03aff 127.53%
  );
  height: 100vh;
  width: 100vw;
  display: grid;
  grid-template-columns: 50px 1fr 50px;
  grid-template-rows: 10vh 50vh 40vh;
  grid-template-areas:
    ". . ."
    ".card ."
    ". button .";
}
.card-wrapper {
  width: 300px;
  height: 300px;
  /* background: white; */
  grid-area: card;

  justify-self: center;
}
.card {
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: space-around;
  flex-direction: column;
  align-items: center;
}

.card h3 {
  font-family: "Monoton", cursive;
  /* font-size: 30px; */

  font-size: 120px;
  background: linear-gradient(180deg, #ff3030 0%, rgba(255, 253, 253, 0) 100%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.sign-up-btn-wrapper {
  grid-area: button;
  justify-self: center;
  width: 100%;
}
.sign-up-btn {
  width: 100%;
  height: 2em;
  border: none;
  background: #cb3f7a;
  color: white;
  font-size: 22px;
}
